/*Dropotron*/

#nav ul { 
	list-style: none;
	margin: 0; 
	padding: 0;
}

#nav ul li { 
	display: inline-block; 
	margin: 0 0.1em 0 0.1em; 
	padding: 0.35em 0.15em 0.35em 0.15em; 
	border-radius: 0.5em;
}

/*#nav ul li.active { 
	background: #999; 
}*/

#nav ul li.active a{ 
	color: #b85d26;
	text-decoration: none; 
}

.dropotron { 
	background: rgb(255, 204, 102); 
	border-radius: 0 0 0.5em 0.5em; 
	list-style: none; 
	margin: -1px; 
	min-width: 10em; 
	padding: 0.75em 1em 0.75em 1em;
	border: 1px solid #be6127; 
}

.dropotron.level-0.right{
	margin: 1px;
	margin-top: -1px;
}

.dropotron > li { 
	border-top: solid 1px rgb(190, 97, 39); 
	margin: 0; 
	padding: 0; 
}

.dropotron > li:first-child { 
	border-top: 0; 
}

.dropotron > li > a { 
	color: rgb(190, 97, 39); 
	display: block; 
	padding: 0.5em 0 0.5em 0; 
	text-decoration: none; 
}


.dropotron > li.active > a, .dropotron > li:hover > a { 
	color: #fff; 
}

/*.dropotron.level-0 { 
	margin-top: 1.25em; 
}*/

.dropotron.level-0:before { 
	content: ''; 
	position: absolute; 
	border-bottom: solid 0.5em #444; 
	border-left: solid 0.5em transparent; 
	border-right: solid 0.5em transparent; 
	top: -0.5em; 
}

#nav ul li.opener.active{
	background: rgb(255, 204, 102);
	border-radius: 0.5em 0.5em 0 0;

}

.dropotron.level-0 a:hover{
	color: #fff !important;
}

/*Pagination*/
.paginatorWrapper{
	width: 100%;
	text-align: center;
}

ul.pagination{
	display: inline-block;
	padding: 0;
	margin: 50px auto;
}

ul.pagination li{
	display: inline;
	margin-right: 15px;
	text-align: center;
}

ul.pagination li a, ul.pagination li span{
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
}

.pagination li:first-child a, .pagination li:first-child span{
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
}

.pagination li:last-child a, .pagination li:last-child span{
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
}

ul.pagination li.active span{
    background-color: rgba(139, 132, 41, 0.7);
    color: rgba(255, 204, 102, 0.75);
}

ul.pagination li:hover:not(.active) {
	background-color: #ddd;
}

ul.pagination li a, ul.pagination li span{
	border: 1px solid rgb(190, 97, 39);
}

ul.pagination li a:hover{
	color: rgb(190, 97, 39) !important;
}


/*Slide-out menu*/
.nav-side{
	position: fixed;
	left: 0;
	top: 0;
	height: 500px;
	width: 100%;
	max-width: 250px;
	box-sizing: border-box;
	padding: 20px;
	color: #fff;
	background-color: rgb(139, 132, 41);
	z-index: 1;
	margin-left: -250px;
	transition: margin 200ms ease-in-out;
	display: none;
}

.nav-side.nav-open{
	margin-left: 0;
	box-shadow: 1px 0 3px rgba(0, 0, 0, .1);
}

.nav-side ul{
	margin-top: 30px;
	line-height: 40px;	
}

.nav-side ul li a{
	color: rgb(255, 204, 102);
	text-decoration: none;
	font-size: 1.4em;
}

.nav-side ul li a:hover{
	color: #fff !important;
}

.nav-toggle{
	position: absolute;
	right: -35px;
	top: 0;
	width: 35px;
	height: 35px;
	line-height: 40px;
	text-align: center;
	background-color: rgb(139, 132, 41);
	border-bottom-right-radius: 5px;
	box-shadow: 1px 0 3px rgba(0, 0, 0, .1);
}

.nav-toggle i{
	color: rgba(255, 204, 102, 0.75);
}

@media screen and (max-width: 1024px) {
    nav#nav{
    	display: none;
    }

    div.nav-side{
    	display: initial;
    }
}

